import React from 'react';


const PrimaryDate = (props) => {
    
    const dayMap = ['Sunday','Monday','Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday']
    const time = new Date(props.date)
    const day =dayMap[time.getDay()]
    const date = time.getDate()
    const suffix =  date === 1 || date === 21 || date === 31 ? `st`
                   : date === 2 || date === 22 ? `nd` 
                   : date === 3 || date === 23 ? `rd` : `th`;
    const primaryDate = props.primary === 0 ? `Today` : props.primary === 1 ? `Tomorrow` : `${day} ${date} ${suffix}`;

    return <h2>{primaryDate}</h2>
}

export default PrimaryDate;