import React from 'react';
import styled from 'styled-components';

const WeathCardContainer = styled.div`
    width: auto;
    background: none;
    border-radius: 10px;
    text-align: left;
    padding: 10px;
    transition: 0.3s;
    &:hover{
        cursor: pointer;
        background: rgba(0,0,0,0.3);
    }
`
const WeatherCard = (props) => {
    const time = new Date(props.date*1000)
    const date = time.getDate()
    const suffix =  date === 1 || date === 21 || date === 31 ? `st`
                   : date === 2 || date === 22 ? `nd` 
                   : date === 3 || date === 23 ? `rd` : `th`;

    return (
        <WeathCardContainer onClick={props.clicked}>
            <p>{props.today === 0 ? `Today` : 
                props.today === 1 ? `Tomorrow` : `${date}${suffix}`}</p>
        </WeathCardContainer>
    )
}

export default WeatherCard