import React, {useState,useEffect,useContext} from 'react';
import axios from 'axios';
import styled from 'styled-components';
import PrimaryWeather from '../components/primaryWeather';
import PrimaryDate from '../components/primaryDate';
import WeatherCard from '../components/weatherCard';
import {PositionContext,ThemeContext} from '../context/context';
import {date} from '../App';
import LocationMapper from '../components/locationMapper';
import Spinner from '../components/spinner';
import Clear from '../assets/clear.jpg';
import Clouds from '../assets/overcast.jpg';
import Rain from '../assets/rain.jpg';
import Thunderstorm from '../assets/storm.jpg';
import Overcast from '../assets/clear.jpg';

const apiKey = `d62c3e3ede49b65f11eceeb16ca5c078`;

const backgroundMap = {'Clear': Clear, 'Rain': Rain, 'Overcast': Overcast, 'Thunderstorm' : Thunderstorm, 'Clouds': Clouds}

const WeeklyWeatherContainer = styled.section`
    position: relative;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: start;
    width: 100%;
    margin: auto;
    h2{
        color: white;
        font-weight: 300;
        padding-left: 20px;
    }
`
const DateWrapper = styled.div`
    background: rgba(0,0,0,0.7);
    overflow-x: scroll;
    display: flex;
    flex-direction: row;
    z-index: 1000;
`

const WeeklyWeather = () => {
    const {theme, setTheme} = useContext(ThemeContext)
    const {position, setPosition} = useContext(PositionContext)
    const [forecast, setForecast] = useState(undefined)
    const [primary, setPrimary] = useState(0)


  useEffect(() => {
    if(sessionStorage.getItem(`${position}-${date}`)){
        let sessionData = JSON.parse(sessionStorage.getItem(`${position}-${date}`))
        setForecast(sessionData)
        console.log('Using Session Storage')
    } else { 
        if(position === 'userLocation') {
            const fetchData = async () => {  
                navigator.geolocation.getCurrentPosition((geo) => {
                    const params = {lon: geo.coords.longitude, lat: geo.coords.latitude, appid: apiKey}
                    axios.get('https://api.openweathermap.org/data/2.5/onecall?', {params})
                    .then((response) => {
                            sessionStorage.setItem(`${position}-${date}`,JSON.stringify(response.data.daily))
                            setForecast(response.data.daily)
                            console.log('New Call')})
                })
            }
            fetchData()

        } else {
            let location = LocationMapper(position)
            const fetchData = async () => {
                let params = {lat: location[0].lat,lon: location[0].lng, appid: apiKey}
                const response = await axios.get('https://api.openweathermap.org/data/2.5/onecall?', {params})
                sessionStorage.setItem(`${position}-${date}`,JSON.stringify(response.data.daily))
                setForecast(response.data.daily)
                console.log('New Call')
            }
            fetchData()
        }
    }
  },[position])

  const cardClickHandler = (i) => {
    setPrimary(i)
    setTheme(backgroundMap[forecast[primary].weather[0].main])
  }

    return (
        <>
        <WeeklyWeatherContainer>
            {forecast ? 
                <PrimaryDate
                    date={forecast[primary].dt*1000}
                    primary={primary}
                /> : <Spinner/>}
            {forecast ? 
                <PrimaryWeather
                    location={position === 'userLocation' ? 'Current Location' : position}
                    date={forecast[primary].dt}
                    temp={(forecast[primary].temp.day - 273.15).toFixed(2)}
                    description={forecast[primary].weather[0].description}
                    sunset={forecast[primary].sunset}
                    sunrise={forecast[primary].sunrise}
                    wind={forecast[primary].wind_speed}
                    id={forecast[primary].weather[0].id}
               />   
             : <Spinner/>}
        </WeeklyWeatherContainer>
        <DateWrapper>
            {forecast ? forecast.map((e,i) => (
                <WeatherCard
                    clicked={() => cardClickHandler(i)}
                    key={i}
                    date={e.dt}
                    today={i}
                    main={e.weather[0].main}
                    />
            )) 
            : <Spinner/> }
         </DateWrapper>
        </>
    )
}

export default WeeklyWeather