import React, {useState} from 'react';
import WeeklyWeather from './containers/WeeklyWeather';
import Search from './components/search';
import {ThemeContext, PositionContext} from './context/context';
import space from './assets/space.jpg';
import './App.scss';

const d = new Date();
const day = d.getDate()
const month = d.getMonth()
const year = d.getFullYear()
export const date = `${day}/${month}/${year}`


function App() {
  const [theme, setTheme] = useState(space)
  const [position, setPosition] = useState(`userLocation`)

  return (
    <ThemeContext.Provider value={{theme,setTheme}}>
      <div className="App">
        <img className="Background" src={theme} alt='Current weather'/>
        <h1>Weather App v1.0</h1>
        <PositionContext.Provider value={{position,setPosition}}>   
          <Search/>
          <WeeklyWeather/>
        </PositionContext.Provider>
      </div>
    </ThemeContext.Provider>
  );
}

export default App;
