import React from 'react';
import styled from 'styled-components';
import {IconMap} from './iconMap';

const WeatherContainer = styled.div`
    display: flex;
    flex-direction: column;
    width: 100%;
    text-align: left;
    .top{
        display: flex;
        flex-direction: row;
        .col-one{
            padding: 10px;
            width: 50%;
            svg{
                width: 100%;
                height: 100%;
                fill: white;
            }
        }
        .col-two{
            display: flex;
            flex-direction: column;
            padding: 10px;
            width: 50%;
            justify-content: space-evenly;
        p{
            width: 100%;
            text-transform: capitalize; 
            font-weight: 300; 
            margin: 10px;
        }
        p:nth-of-type(1) {
                font-size: 30px;
            }
        p:nth-of-type(2) {
                font-family: 'Times New Roman';
                font-style: italic;
                font-size: 25px;
            }
        }
    }
    .bottom{
        display: flex;
        flex-direction: row;
        text-align: center;
        p {
            font-size: 16px;
            width: 50%;
            svg{
                width: 20px;
                height: 20px;
            }
        }
    }
    `

const PrimaryWeather = (props) => {
    const rise = new Date(props.sunrise*1000)
    const a = rise.getHours().toString().length === 1 ? `0${rise.getHours()}` : rise.getHours();
    const b = rise.getMinutes().toString().length === 1 ? `0${rise.getMinutes()}` : rise.getMinutes();

    const set = new Date(props.sunset*1000)
    const c = set.getHours().toString().length === 1 ? `0${set.getHours()}` : set.getHours();
    const d = set.getMinutes().toString().length === 1 ? `0`+ set.getMinutes() : set.getMinutes();

    return (
            <WeatherContainer>
                <div className='top'>
                    <div className="col-one">
                        {IconMap[props.id]}
                    </div>
                    <div className="col-two">
                        <p><mark>{props.temp} &#x2103;</mark></p>
                        <p>{props.description}</p>
                    </div>
                </div>
                <div className='bottom'>
                    <p>{IconMap['sunrise']} {a}:{b} am</p>
                    <p>{IconMap['sunset']} {c}:{d} pm</p>
                    <p>{IconMap['wind']} {(props.wind*2.237).toFixed()} mph</p>
                </div>
            </WeatherContainer>
    )
}

export default PrimaryWeather;