import React, {useState, useEffect, useContext} from 'react';
import {PositionContext} from '../context/context';
import styled from 'styled-components';
import locations from './gb.json';

const sortedLocations = locations.sort((a,b) => (a.city > b.city) ? 1 : (b.city > a.city) ? -1 : 0);

const StyledSearch = styled.section`
    position: relative;
    width: 100%;
    margin: 10px auto;
    input{
        text-align: center;
        font-family: Times New Roman;
        color: white;
        border: none;
        padding: 20px;
        font-size: 25px;
        background: none;
        width: 100%;
        &::placeholder{
            color: white;
            font-weight: 100;
        }
    }
`

const DropDownList = styled.ul`
    position: absolute;
    background:rgba(255,255,255,0.6);
    padding: 0;
    top: 70px;
    left: 0;
    width: 100%;
    list-style: none;
    transition: 0.2s;
    max-height: ${props => props.toggle ? `350px` : `0`};
    overflow: scroll;
    border-radius: 5px;
    z-index: 1000;
`
const ListItem = styled.li`
    width: 100%;
    color: black;
    margin: 0;
    padding: 20px;
    transition: 0.2s;
    border-bottom: 1px solid silver;
    &:hover{
        cursor: pointer;
        background: rgba(255,255,255,0.8);
    }
`

const Search = () => {
    const {position, setPosition} = useContext(PositionContext)
    const [input, setInput] = useState('')
    const [list, setList] = useState(sortedLocations)
    const [toggle, setToggle] = useState(false)
    const [listSelection, setListSelection] = useState(undefined)

    useEffect(() => {
        input.length >= 1 && input !== listSelection ? setToggle(true) : setToggle(false);
        let newList = sortedLocations.filter((a) => (a.city).toUpperCase().includes((input).toUpperCase()))
        setList(newList)
    }, [input,listSelection])

    const clickHandler = (e) => {
        setInput(e)
        setListSelection(e)
        setToggle(false)
    }

    useEffect(() => {
        if(listSelection !== undefined) {
            setPosition(listSelection)
        }
    }, [listSelection, setPosition])

    return (
        <StyledSearch>
            <input type='text' onChange={(e) => setInput(e.target.value)} autoFocus value={input} placeholder='Change Location...'/>
            <DropDownList toggle={toggle}>
                {list.map((e,i) => (
                   <ListItem onClick={() => clickHandler(e.city)} key={i}>{e.city}</ListItem> 
                ))}
            </DropDownList>
        </StyledSearch>
    )
}

export default Search;